<template>
  <div class="tab-control">
    <!-- tab-control -->

    <!-- div ,itemtab-control-item,index,click-->


    <!-- <div v-for="items in titles" :key="items">
   <span>{{item}}</span> --> 

    <div v-for="(item, index) in titles" :key="item"
         class="tab-control-item"
         :class="{active: index === currentIndex}"
         @click="itemClick(index)" >
      <span>{{item}}</span>




    </div>
  </div>
</template>

<script>
  export default {
    name: "TabControl",
    props: {
      // 
      titles: {
        type: Array,
      // 

        default() {
          return []
        }
      }
    },
    data() {
      return {
        currentIndex: 0
      }
     },
    // current



    // 
    methods: {
      itemClick(index) {
        this.currentIndex = index;
        // HOME$emittabclick,index,
        this.$emit('tabClick', index)
      }
    }
  }
</script>

<style scoped>
  .tab-control {
    display: flex;
    text-align: center;
    font-size: 15px;
    height: 40px;
    line-height: 40px;
    background-color: #fff;
  }

  .tab-control-item {
    flex: 1;
  }

  .tab-control-item span {
    padding: 5px;
  }

  .active {
    color:  rgb(9, 104, 182);
  }

  .active span {
    /* border-bottom: 3px solid var(--color-tint); */
      border-bottom: 3px solid rgb(9, 104, 182);
  }
</style>
