<template>
<div class="detail">
    <detailswiper :topimges="topimges"/>
    <!-- topimges="topimges, -->

</div>
  
</template>

<script>
import {getdetail} from '../../network/detail';
import detailswiper from './detailswiper';
export default {
name :"detail",
components:{
    detailswiper
    },

data(){
    // iidnull
    return{
        iid: null,
        topimges:[]

    }
},
created(){
    // iid,iid
    this.iid = this.$route.params.iid
    // iidnetworkdetail.js

    // network,,
    // 1.
    //            2.  
    getdetail(this.iid).then(
        res=>{
            console.log(res);
            // detailswiper
            this.topimges = res.result.items.topimges
            


            
        }
    )
}
}
</script>

